import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";

export const loadWeather = createAsyncThunk(
    "weather/loadWeather",
    async({lat, long}) => {
        const link = `https://community-open-weather-map.p.rapidapi.com/weather?lat=${lat}&lon=${long}&units=metric`;
        const data = await fetch(link, {
            "method": "GET",
            "headers": {
                "x-rapidapi-host": "community-open-weather-map.p.rapidapi.com",
                "x-rapidapi-key": "c745030bd5msh840be0eeb1d3a7dp161562jsn29910ef0bba7"
            }
        });
        const json = await data.json();
        return json;
    }
);

export const weatherSlice = createSlice({
    name: 'weather',
    initialState: {
        weather: [],
        isLoading: false,
        hasError: false,
    },
    reducers: {},
    extraReducers: {
        [loadWeather.pending]: (state) => {
            state.isLoading = true;
            state.hasError = false;
        },
        [loadWeather.fulfilled]: (state, action) => {
            state.weather.push(action.payload);
            state.isLoading = false;
            state.hasError = false;
        },
        [loadWeather.rejected]: (state) => {
            state.isLoading = false;
            state.hasError = true;
        }
    }
});

export default weatherSlice.reducer;
export const selectWeather = state => state.weather.weather;